<template>
<div class="hot_header_module">
  <div class="hot_header">
    <div class="hot_header_left logo" @click="toPage('homepage')"></div>
    <div class="hot_header_right hot_header_row">
      <div :class="['hot_header_col', currRouteName === 'lp' ? 'is_active' : '']" @click="toPage('lp')">LP区</div>
      <div :class="['hot_header_col', currRouteName === 'pledge' ? 'is_active' : '']" @click="toPage('pledge')">质押池</div>
      <!-- <div :class="['hot_header_col', currRouteName === 'dao' ? 'is_active' : '']" @click="toPage('dao')">DAO</div> -->
      <div :class="['hot_header_col', currRouteName === 'mine' ? 'is_active' : '']" @click="toPage('mine')">我的</div>
    </div>
  </div>
  <div class="hot_header">
    <div class="hot_header_left"></div>
    <div class="hot_header_right hot_header_row">
      <div class="hot_header_col address" @click="toPage('mine')">
        <!-- <span class="is_enabled">已激活</span> -->
        <span class="not_enabled">未激活</span>
       {{address | addressFormat}}
      </div>
      <div class="hot_header_col tag">EN</div>
    </div>
  </div>
</div>

</template>

<script>

export default {
  data() {
    return this.$Config
  },

  mounted() {
    this.$getAddress()
  },


  computed: {
    currRouteName() {
      return this.$route.name
    },
  },

  filters: {
    addressFormat(addr) {
      if (!addr) return ''
      const newAddr = addr
      if (newAddr.length > 8) {
        return `${newAddr.substr(0, 4)}...${newAddr.substr(newAddr.length-4, 4)}`
      }
      return addr
    }
  },
  methods: {
    toPage(name) {
      // if (name === 'dao') return Message.warning({
      //   type: 'warning',
      //   message: '暂未开放',
      //   customClass: 'notity_message',
      //   showClose: true,
      //   // duration: 0
      // })
      this.$router.push({ name})
    }  
  }
}
</script>

