import Vue from 'vue'
import App from './App.vue'
import Element from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import  { BigNumber }  from 'bignumber.js'
Vue.prototype.BigNumber = BigNumber;

import Config from './assets/config'
Vue.prototype.$Config = Config;

import "@/assets/scss/app.scss"






import Web3 from 'web3'
Vue.prototype.Web3 = Web3

var I3=async function() {
  if(window.ethereum) {
    window.web3 = new Web3(ethereum);
    try {
      await ethereum.enable();
      this.web3Provider = ethereum;
    } catch(error) {}
  } else if(window.web3) {
    this.web3Provider = web3.currentProvider;
    window.web3 = new Web3(web3.currentProvider);
  } else {
    console.log('Non-Ethereum browser detected. You should consider trying MetaMask!');
  }
};
I3();
Vue.prototype.$I3 = I3;

Vue.filter("multipUnit", function(value) {
  value =BigNumber(value)
  return value.multipliedBy(BigNumber(10).pow(18))
});

function getAddress() { //获取地址
  web3.eth.getAccounts().then(res => this.address=res[0])
}
Vue.prototype.$getAddress = getAddress;

import router from './router'
Vue.use(Element)
Vue.config.productionTip = false



new Vue({
  render: h => h(App),
  router,
}).$mount('#app')
